import React from "react";
function CustomButton(props) {
    return (
        <React.Fragment>
            <button
                type="button"
                className="btn btn-outline-primary"
                style={{
                    padding: "5px 20px",
                    borderRadius: "25px",
                    outline: "none",
                    boxShadow: "0px 5px 8px grey",
                }}
                onClick={props.clickHandle}
            >
                {props.buttonName}
            </button>
        </React.Fragment>
    );
}

export default CustomButton;
